
@font-face {
    font-family: 'Bebas';
    src: url('../../fonts/BebasNeue-Regular.woff2') format('woff2'),
        url('../../fonts/BebasNeue-Regular.woff') format('woff');
    font-weight: normal;
    font-style: normal;
    font-display: swap;
  }

  @font-face {
    font-family: 'Sansita';
    src: url('../../fonts/Sansita-Regular.woff2') format('woff2'),
        url('../../fonts/Sansita-Regular.woff') format('woff');
    font-weight: normal;
    font-style: normal;
    font-display: swap;
  }

  @font-face {
    font-family: 'Proza';
    src: url('../../fonts/ProzaLibre-Regular.woff2') format('woff2'),
        url('../../fonts/ProzaLibre-Regular.woff') format('woff');
    font-weight: normal;
    font-style: normal;
    font-display: swap;
  }

@mixin flex-row {
    display: flex;
    justify-content: center;
    align-items: center;
}

@mixin flex-column {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

@mixin complete {
    height: 100%;
    width: 100%;
}

@mixin paginating {
    @include flex-row();
        .pagination {
            @include flex-row();
            .page-item {
                .link {
                    @include flex-column();
                    cursor: pointer;
                    height: 25px;
                    width: 26px;
                    padding-top: 2px;
                }
                .page {
                    // background-color: black;
                    color: rgba(255, 255, 255, 0.8);
                }
                .active {
                    background-color: rgba(122, 121, 121, 0.842);
                    color: white;
                }
            }
        }
}

ul {
    padding: 0;
}

$image-height: 180px;
$image-width: 180px;
$top-disc: ($image-height)/(2) ;
$left-disc: ($image-width)/(2) ;

